
#consent .title-bar {
  background: whitesmoke;
  padding: 20px;
  border-bottom: 1px solid lightgray;
  text-align: center;
}
#consent .title-bar h1 {
    padding: 0;
    margin: 0;
}
#consent .texto {
  padding: 0 20px;
  font-size: 11pt;
}
#consent .signature {
  text-align: center;
  font-family: 'Open Sans', sans-serif;
  line-height: 15pt;
}
#consent .bold {
  font-weight: bold;
}
#consent .pagination {
  text-align: center;
  padding: 20px;
  background: whitesmoke;
  border-top: 1px solid lightgray;
}
#consent .pagination span {
    margin: 0 10px;
}



